import {Component, OnInit, ViewChild} from '@angular/core';
import {AbstractPageComponent, ConfirmDialog} from '../../../ic-core';
import {Address, Customer, CustomerService, Phone} from '../../../generated-api';

import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';
import {MatDialog} from '@angular/material';

const title = 'Customers';
const breadcrumb = [{title: 'Account'}, {title: 'Customers'}];
function compare(a: number | string, b: number | string, isAsc: boolean) {
  return (a < b ? -1 : 1) * (isAsc ? 1 : -1);
}

const NAMES: string[] = [
  'Maia', 'Asher', 'Olivia', 'Atticus', 'Amelia', 'Jack', 'Charlotte', 'Theodore', 'Isla', 'Oliver',
  'Isabella', 'Jasper', 'Cora', 'Levi', 'Violet', 'Arthur', 'Mia', 'Thomas', 'Elizabeth'
];

@Component({
  selector: 'ic-customers',
  templateUrl: './customers.component.html',
  styleUrls: ['./customers.component.scss']
})
export class CustomersComponent extends  AbstractPageComponent implements OnInit {

  public searchCondition = {
    searchString: '',
    periodFrom: new Date(),
    periodTo: new Date(),
    filterBy: 'TODAY_APPOINTMENTS'
  };

  displayedColumns: string[] = ['photo', 'id', 'fullName', 'phone', 'email',  'address', 'actions'];
  dataSource: MatTableDataSource<Customer>;

  public customers: Customer[] = [];

  constructor(private customerApi: CustomerService, public dialog: MatDialog) {
    super(title, breadcrumb);
  }

  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;


  ngOnInit() {
    const users = Array.from({length: 100}, (_, k) => createNewUser(k + 1));

    // Assign the data to the data source for the table to render
    this.dataSource = new MatTableDataSource(users);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  deleteCustomer(customer: Customer) {
    const dialogRef = this.dialog.open(ConfirmDialog, {
      width: '300px',
      data: `Do you want to delete "${customer.fullName}" (id: ${customer.id})?`
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        const index: number = this.dataSource.data.findIndex(item => item.id === customer.id);
        this.dataSource.data.splice(index, 1 );
        this.dataSource = new MatTableDataSource<Customer>(this.dataSource.data);
      }
    });
  }
}

/** Builds and returns a new User. */
function createNewUser(id: number): Customer {
  const name = NAMES[Math.round(Math.random() * (NAMES.length - 1))] + ' ' +
    NAMES[Math.round(Math.random() * (NAMES.length - 1))].charAt(0) + '.';

  const address: Address = new Address();
  address.city = 'city city';
  address.district = 'district district ';
  address.street = 'street street';
  return {
    id: id.toString(),
    fullName: name,
    email: Math.round(Math.random() * 100).toString() + '@email.com',
    photo: 'assets/images/xs/avatar.jpg',
    addresses: [address],
    phoneNumbers: [{
      number: '123456789',
      primary: true
    }]
  };
}

