import {Component, OnInit} from '@angular/core';
import {CustomerApi} from '../../services/customer.api';
import {ActivatedRoute} from '@angular/router';
import {AbstractPageComponent} from '../../../ic-core/pages/abstract-page/abstract-page.component';
import {Address, Customer} from '../../../generated-api';


const title = 'Create Customer';
const breadcrumb = [{title: 'Account'}, {link: '/accounts/customers', title: 'Customers'}, {title: 'Create Detail'}];


@Component({
  selector: 'ic-new-customer',
  templateUrl: './new-customer.component.html',
  styleUrls: ['./new-customer.component.scss']
})
export class NewCustomerComponent extends AbstractPageComponent implements OnInit {

  services: Array<string> = ['Botox', 'Filler', 'Da'];
  customer: Customer;

  constructor(private customerApi: CustomerApi, private route: ActivatedRoute) {
    super(title, breadcrumb);
  }

  ngOnInit() {
    const address: Address = new Address();
    address.city = '';
    address.district = '';
    address.street = '';
    this.customer = {
      fullName: '',
      email: Math.round(Math.random() * 100).toString() + '@email.com',
      photo: 'assets/images/xs/avatar.jpg',
      addresses: [address],
      phoneNumbers: [{
        number: '',
        primary: true
      }]
    };
  }

  public updateProfileId() {

  }
}
