import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {CustomerApi} from '../../services/customer.api';
import {ActivatedRoute, Router} from '@angular/router';
import {Customer} from '../../../generated-api';


@Component({
  selector: 'ic-customer-profile',
  templateUrl: './customer-information.component.html',
  styleUrls: ['./customer-information.component.scss']
})
export class CustomerInformationComponent implements OnInit {

  @Input() editMode: boolean;
  @Input() customer: Customer;
  @Output() onDataUpdate = new EventEmitter<void>();

  private updateProperties = {};

  public genders = [{value: true, title: 'Male'}, {value: false, title: 'Female'}];

  constructor(private customerApi: CustomerApi, private route: ActivatedRoute, private router: Router) {
  }

  ngOnInit() {
    this.editMode = !!this.editMode;
  }

  private async update() {
    if (!!this.customer.id) {
      await this.customerApi.update(this.customer.id, this.updateProperties);
    } else {
      let id = 12345;//await this.customerApi.create(this.customer);
      //await this.router.navigate(['/accounts/customers',  id]);
    }
    this.onDataUpdate.emit();
  }

  public changeProperty(propertyName: string) {
    this.updateProperties[propertyName] = this.customer[propertyName];
  }

  public toggleEdit() {
    if (this.editMode) {
      this.update();
    }
    this.editMode = !this.editMode;
  }

}
