import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Address, Customer, CustomerService} from '../../generated-api';

@Injectable({
  providedIn: 'root'
})
export class CustomerApi {
  private data: Customer[] = [];

  constructor(protected customerService: CustomerService) {
    this.data = Array.from({length: 100}, (_, k) => createNewUser(k + 1));
  }


  public async search(query: object): Promise<Customer[]> {
    let customers: Customer[] = [];
    return customers;
  }

  public async get(id: string): Promise<Customer> {
    let customer: Customer = await this.customerService.getCustomerInstance(+id).toPromise();
    return customer;
  }


  async update(id: any, updateProperties: {}) {
  }
}

const NAMES: string[] = [
  'Maia', 'Asher', 'Olivia', 'Atticus', 'Amelia', 'Jack', 'Charlotte', 'Theodore', 'Isla', 'Oliver',
  'Isabella', 'Jasper', 'Cora', 'Levi', 'Violet', 'Arthur', 'Mia', 'Thomas', 'Elizabeth'
];

function createNewUser(id: number): Customer {
  const name = NAMES[Math.round(Math.random() * (NAMES.length - 1))] + ' ' +
    NAMES[Math.round(Math.random() * (NAMES.length - 1))].charAt(0) + '.';

  const address: Address = new Address();
  address.city = 'city city';
  address.district = 'district district ';
  address.street = 'street street';
  return {
    id: id.toString(),
    fullName: name,
    email: Math.round(Math.random() * 100).toString() + '@email.com',
    photo: 'assets/images/xs/avatar.jpg',
    addresses: [address],
    phoneNumbers: [{
      number: '123456789',
      primary: true
    }]
  };
}
